.nav-bar {
    background: var(--background);
    height: 41px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
    .nav-link {
        padding: 0 10px;
        position: relative;
        left: 0;
        &,
        a {
            color: var(--text);
            text-decoration: none;
            font-size: 16px;
            text-transform: uppercase;
        }
        span {
            margin: 0 5px;
            display: none;
        }
        label {
            cursor: pointer;
            &::after {
                content: "Light";
            }
        }
        .nav-link-active {
            color: var(--primaryBox);
        }
    }
}