:root {
  --primary: #bb86fc;
  --primaryBox: #775f97;
  --primaryVariant: #3700b3;
  --secondary: #03dac6;
  --secondaryVariant: #03dac6;
  --background: #121212;
  --x-background: #2e2e2e;
  --text: #ffffff;
  --projectBackground: #ececec;
}

* {
  margin: 0;
  padding: 0;
}

.fullbody {
  background: var(--background);
  font-family: "Lato", sans-serif;
  min-height: 100vh;
  position: relative;
}

.cursor {
  position: fixed;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 60px 60px var(--text);
          box-shadow: 0 0 60px 60px var(--text);
  background: var(--text);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.left,
.right {
  position: absolute;
  height: 100%;
  background: var(--background);
}

.left {
  left: 0;
}

.right {
  top: 0;
  right: 0;
}

.bottom {
  background: var(--background);
}

.main {
  max-width: 1024px;
  position: relative;
  margin: 0 auto;
}

#color {
  display: none;
}

#color:checked ~ .fullbody .main .nav-bar span label::after {
  content: "Dark";
}

#color:checked ~ .fullbody {
  --primary: #6200ee;
  --primaryBox: #d3d3d3;
  --primaryVariant: #3700b3;
  --secondary: #03dac6;
  --secondaryVariant: #018786;
  --background: #ffffff;
  --x-background: #ececec;
  --text: #272727;
  --projectBackground: #d1d1d1;
}

