@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap");
@import "./scss_files/navbar";
@import "./scss_files/designs";
@import "./scss_files/projects";
@import "./scss_files/homepage";

:root {
    --primary: #bb86fc;
    --primaryBox: #775f97;
    --primaryVariant: #3700b3;
    --secondary: #03dac6;
    --secondaryVariant: #03dac6;
    --background: #121212;
    --x-background: #2e2e2e;
    --text: #ffffff;
    --projectBackground: #ececec;
}
* {
    margin: 0;
    padding: 0;
}

.fullbody {
    background: var(--background);
    font-family: "Lato", sans-serif;
    min-height: 100vh;
    position: relative;
}

.cursor {
    position: fixed;
    border-radius: 50%;
    box-shadow: 0 0 60px 60px var(--text);
    background: var(--text);
    transform: translate(-50%, -50%);
}

.left,
.right {
    position: absolute;
    height: 100%;
    background: var(--background);
}

.left {
    left: 0;
}

.right {
    top: 0;
    right: 0;
}

.bottom {
    background: var(--background);
}

.main {
    max-width: 1024px;
    position: relative;
    margin: 0 auto;
}

#color {
    display: none;
    &:checked ~ .fullbody .main .nav-bar span label::after {
        content: "Dark";
    }
    &:checked ~ .fullbody {
        --primary: #6200ee;
        --primaryBox: #d3d3d3;
        --primaryVariant: #3700b3;
        --secondary: #03dac6;
        --secondaryVariant: #018786;
        --background: #ffffff;
        --x-background: #ececec;
        --text: #272727;
        --projectBackground: #d1d1d1;
    }
}

.design-row-hover {
    transition: 0.5s linear;
    transform: translate(-256px, 0);
}



@mixin education-animation($h4, $p, $li) {
    @keyframes #{$h4} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
    @keyframes #{$p} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
    @keyframes #{$li} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
}

@include education-animation(educationClgh4, educationClgp, educationClgli);
@include education-animation(educationHsh4, educationHsp, educationHsli);
@include education-animation(educationItmh4, educationItmp, educationItmli);

@keyframes fixed {
    0% {
        left: -200px;
    }
}

@keyframes S-fixed {
    0% {
        right: -200px;
    }
}

@keyframes fixed-img {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

@keyframes scroll-text {
    from {
        top: -4px;
        opacity: 0;
    }
    to {
        top: 12px;
        opacity: 1;
    }
}

@keyframes skill-box {
    from {
        transform: scale(0.1);
    }
    to {
        transform: scale(1);
    }
}
