.Home {
    background: var(--background);
    font-family: "Roboto", sans-serif;
    position: relative;
    .home-container1 {
        position: relative;
        padding: 0 10px;
        height: calc(100vh - 51px);
        letter-spacing: 2px;
        display: flex;
        align-items: center;
        .extrahome,
        .extrahome1 {
            position: absolute;
            border-radius: 15px;
            background: var(--primaryBox);
            top: 50%;
            transform: translateY(-50%) rotateZ(45deg);
        }
        .extrahome {
            left: 95px;
            height: 155px;
            width: 155px;
            opacity: 0.2;
        }
        .extrahome1 {
            right: 75px;
            height: 250px;
            width: 250px;
            opacity: 0.3;
        }
        div {
            h1 {
                font-size: 40px;
                font-weight: 800;
                line-height: 55px;
                color: var(--text);
                .primary {
                    color: var(--primaryBox);
                }
            }
            h6 {
                font-size: 12px;
                font-weight: 100;
                color: var(--text);
            }
        }
        .contact {
            position: absolute;
            bottom: 20px;
            left: 10px;
            img {
                width: 15px;
                height: 15px;
                margin: 0 10px;
            }
        }
        .scroll-text {
            position: absolute;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            width: 100px;
            height: 30px;
            border-radius: 15px;
            background: var(--primaryBox);
            display: flex;
            align-items: center;
            padding-left: 20px;
            p {
                display: flex;
                & > span {
                    margin: 0 5px;
                    display: flex;
                    flex-direction: column;
                    position: relative;
                    span {
                        position: absolute;
                        font-size: 12px;
                        margin: -5px 0;
                        font-weight: 700;
                        transform: scaleX(1.5);
                        &:nth-child(1) {
                            top: 0;
                            animation: scroll-text 2s linear infinite;
                        }
                        &:nth-child(2) {
                            top: 10px;
                            animation: scroll-text 2s linear 1s infinite;
                        }
                    }
                }
            }
        }
    }
    .home-container2 {
        font-family: "Lato", sans-serif;
        overflow: hidden;
        @mixin section($pos, $name, $left, $rel, $shdw) {
            transition: 0.5s;
            position: relative;
            #{$left}: 0;
            height: 250px;
            width: 500px;
            background: var(--x-background);
            border: 1px solid transparent;
            margin: 30px 50px;
            float: left;
            border-radius: 10px;
            color: var(--text);
            display: flex;
            @media screen and (max-width: 620px) {
                height: 325px;
                width: 300px;
                flex-direction: column;
            }
            &:hover {
                transition: 0.5s;
                background: none;
                border: 1px solid var(--primaryBox);
                color: var(--text);
            }
            .#{$name}-fixed {
                display: flex;
                flex-direction: column;
                position: $rel;
                top: 30px;
                #{$pos}: -30px;
                border-radius: 10px;
                width: 190px;
                min-height: 190px;
                max-height: 190px;
                background: var(--primaryBox);
                // box-shadow: $shdw 5px 5px var(--primaryBox);
                display: flex;
                align-items: center;
                justify-content: center;
                @media screen and (max-width: 620px) {
                    top: -30px;
                    #{$pos}: 55px;
                }
                span img {
                    width: 130px;
                    height: 130px;
                    color: var(--text);
                }
                h1 {
                    font-size: 20px;
                    font-weight: 900;
                }
            }
            input {
                display: none;
            }
        }
        @mixin labels($pos) {
            height: 60px;
            width: 8px;
            position: absolute;
            #{$pos}: 10px;
            top: 50%;
            transform: translateY(-50%);
            label {
                transition: 0.3s;
                display: block;
                cursor: pointer;
                margin: 5px 0;
                height: 8px;
                width: 8px;
                background: var(--primaryBox);
                border-radius: 4px;
                box-shadow: 0 0 5px 2px var(--primaryBox);
            }
        }
        @mixin containers {
            width: 250px;
            height: 100%;
            display: flex;
            align-items: center;
            overflow: hidden;
            @media screen and (max-width: 620px) {
                align-items: flex-start;
                margin: 0 10px;
            }
        }
        @mixin head {
            font-size: 50px;
            font-weight: 900;
            margin: auto;
            position: relative;
            p {
                color: var(--text);
                text-align: center;
            }
        }
        .education-main {
            position: relative;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-start;
            .education-section {
                @include section(left, education, left, relative, 5px);
                @mixin edu-labels($id, $contenth4, $contentp, $contentli, $animationh4, $animationp, $animationli) {
                    ##{$id}:checked {
                        & ~ .education-labels .education-#{$id} {
                            transition: 0.3s;
                            height: 20px;
                        }
                        & ~ .education-containers .education-container h4::after {
                            content: $contenth4;
                            position: relative;
                            animation: $animationh4 0.35s linear 1;
                        }
                        & ~ .education-containers .education-container p::after {
                            content: $contentp;
                            position: relative;
                            animation: $animationp 0.7s linear 1;
                        }
                        & ~ .education-containers .education-container ul li::after {
                            content: $contentli;
                            position: relative;
                            animation: $animationli 1s linear 1;
                        }
                    }
                }
                @include edu-labels(
                    college,
                    "Pursuing BE Mechanical",
                    "Institute of Engineering And Technology, Indore",
                    "2019 - 2023",
                    educationClgh4,
                    educationClgp,
                    educationClgli
                );

                @include edu-labels(
                    highschool,
                    "High School ( 79.3% )",
                    "Bundelkhand Public Higher Secondary School Hatta, Damoh,MP",
                    "2016 - 2017",
                    educationHsh4,
                    educationHsp,
                    educationHsli
                );

                @include edu-labels(
                    intermediate,
                    "Intermediate ( 73.2% )",
                    "Bundelkhand Public Higher Secondary School Hatta, Damoh,MP",
                    "2018 - 2019",
                    educationItmh4,
                    educationItmp,
                    educationItmli
                );

                .education-labels {
                    @include labels(right);
                }
                .education-containers {
                    @include containers();
                    & > .education-container {
                        h4 {
                            font-size: 22px;
                            font-weight: 700;
                        }
                        p {
                            font-size: 14px;
                            font-weight: 500;
                            margin: 25px 0 10px 0;
                        }
                        ul {
                            font-size: 10px;
                            font-weight: 300;
                            list-style: none;
                        }
                    }
                }
            }
            .education-head {
                @include head();
            }
        }

        .aboutme-main {
            position: relative;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-end;
            .aboutme-section {
                @include section(right, aboutme, right, absolute, -5px);
                @mixin aboutme-labels($id, $contenth4, $contentp, $animationh4, $animationp, $ws) {
                    ##{$id}:checked {
                        & ~ .aboutme-labels .aboutme-#{$id} {
                            transition: 0.3s;
                            height: 20px;
                        }
                        & ~ .aboutme-containers .aboutme-container h4::after {
                            content: $contenth4;
                            position: relative;
                            animation: $animationh4 0.35s linear 1;
                        }
                        & ~ .aboutme-containers .aboutme-container p::after {
                            content: $contentp;
                            position: relative;
                            animation: $animationp 0.7s linear 1;
                            white-space: $ws;
                            line-height: 20px
                        }
                    }
                }

                @include aboutme-labels(
                    page1,
                    "Profile Summary",
                    "Working on it",
                    educationHsh4,
                    educationHsp,
                    none
                );

                @include aboutme-labels(
                    page2,
                    "Top Skills",
                    "Working on it",
                    educationItmh4,
                    educationItmp,
                    pre
                );

                @include aboutme-labels(
                    page3,
                    "Soft Skills",
                    "Working on it",
                    educationClgh4,
                    educationClgp,
                    pre
                );
                .aboutme-labels {
                    @include labels(left);
                }
                .aboutme-containers {
                    @include containers();
                    width: 280px;
                    margin: 0 35px;
                    @media screen and (max-width: 620px) {
                        position: absolute;
                        top: 200px;
                    }
                    & > .aboutme-container {
                        h4 {
                            font-size: 22px;
                            font-weight: 700;
                        }
                        p {
                            font-size: 14px;
                            font-weight: 500;
                            margin: 25px 0 0 0;
                        }
                    }
                }
            }
            .aboutme-head {
                @include head();
            }
        }

        .skill-main {
            .skill-heading {
                padding: 20px;
                h2 {
                    color: var(--text);
                    font-weight: 900;
                    text-align: center;
                }
            }
            .skill-section {
                display: flex;
                justify-content: center;
                .skill-containers {
                    display: flex;
                    flex-wrap: wrap;
                    padding: auto;
                    .skill-container {
                        margin: 20px;
                        width: 200px;
                        height: 200px;
                        &:hover {
                            .skill-box {
                                transition: 0.5s;
                                background: none;
                                border: 1px solid var(--primaryBox);
                                transform: rotateY(180deg);
                                .skill-text,
                                .skill-img {
                                    display: none;
                                }
                                .skill-rating {
                                    transition: 0.5s;
                                    display: initial;
                                    color: var(--text);
                                    transform: rotateY(180deg);
                                }
                            }
                        }
                        .skill-box {
                            position: relative;
                            width: 200px;
                            height: 200px;
                            background: var(--x-background);
                            border: 1px solid transparent;
                            box-sizing: border-box;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            border-radius: 10px;
                            .skill-img {
                                width: 120px;
                                height: 120px;
                                border-radius: 10px;
                                margin: 20px 0;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                background: var(--primaryBox);
                                // box-shadow: 0 0 5px 5px var(--primaryBox);
                                img {
                                    width: 60px;
                                    height: 60px;
                                }
                            }
                            .skill-text {
                                color: var(--text);
                                p {
                                    font-weight: 900;
                                    font-size: 20px;
                                }
                            }
                            .skill-rating {
                                display: none;
                                color: var(--background);
                                text-align: center;
                                margin: auto 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}