@mixin projectdesign($name) {
    transition: 0.5s linear;
    display: flex;
    .remaining-#{$name} {
        background: var(--background);
        border: 1px solid rgba(255, 255, 255, 0);
        outline: 11px solid var(--background);
        margin: 10px;
    }
    .#{$name} {
        position: relative;
        transition: 0.5s;
        border: 1px solid rgba(0, 0, 255, 0.021);
        outline: 11px solid var(--background);
        margin: 10px;
        min-width: 234px;
        height: 300px;
        &:hover {
            transition: 0.5s;
            @media screen and (min-width: 520px) {
                min-width: 490px;
            }
        }
        .#{$name}-container {
            transition: 0.25s ease;
            position: relative;
            height: 100%;
            background-color: var(--background);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            border-radius: 5px;
            @media screen and (min-width: 520px) {
                .#{$name}-name{
                    display: none;
                }
                &:hover{
                    .#{$name}-name{
                        display: initial;
                        z-index: 2;
                        background: radial-gradient(at bottom, black, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
                    }
                }
            }
            .#{$name}-name {
                width: 100%;
                font-weight: 700;
                height: 100px;
                padding: 3px 10px;
                letter-spacing: 2px;
                color: white;
                text-shadow: 0 0 5px black;
                text-transform: uppercase;
                font-size: 18px;
                position: absolute;
                bottom: 0;
                p{
                    position: absolute;
                    bottom: 10px;
                    left: 50%;
                    transform: translateX(-50%);
                }
                &:hover{
                    & ~ img{
                        z-index: 1;
                    }
                }
            }
        }
    }
}

.Designs {
    position: relative;
    z-index: 2;
    .designs {
        overflow-x: hidden;
        .design-row {
            @include projectdesign(design);
            .design-container {
                position: relative;
                img{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    &:first-child{
                        &:hover{
                            & ~ img{
                                z-index: 1;
                            }
                        }
                    }
                    &:nth-child(2){
                        z-index: -1;
                        &:hover{
                            z-index: 1;
                        }
                    }
                }
            }
        }
    }
}